.form-orders {
    padding: 20px;
    border: 1px solid #ccc;
    background-color: #f9f9f9;
    border-radius: 5px;
    width: 50%;
}

.form-orders span{
padding: 15px;
margin: 15px;
}


.form-orders input {
    width: 100%;
    padding: 8px;
    margin: 10px;
    border: 1px solid #ccc;
    border-radius: 3px;
}

.form-orders button {
    background-color: #007bff;
    color: #fff;
    padding: 10px 20px;
    border: none;
    border-radius: 3px;
    cursor: pointer;
}

.form-orders button:disabled {
    background-color: #d74b6e;
}

.form-orders button:disabled:hover {
    background-color: #d74b6e;

}

.form-orders button:hover {
    background-color: #0056b3;
}

@media screen and (max-width: 768px){
    .form-orders{
        width:100%;
    }   
}